import Quill from "quill";

const Embed = Quill.import( "blots/embed" );
const Clipboard = Quill.import( "modules/clipboard" );

/**
 * Escapes strings to make them safe to insert within HTML elements.
 * @name escapeHTML
 * @function
 *
 * @param {String} unsafeString String that needs escaping.
 *
 * @returns {String} Safe for insert string.
 */
function escapeHTML( unsafeString ) {
    unsafeString = unsafeString.replace( /[\&\<\"\']/g, ( m ) => {
        switch ( m ) {
        case "&":
            return "&amp;";
            break;
        case "<":
            return "&lt;";
            break;
        case "\"":
            return "&quot;";
            break;
        case "'":
            return "&apos;";
            break;
        default:
            break;
        }
    });

    return unsafeString;
}

/**
 * Add support for the HTML select element, as an embeded blot for Quill.js.<br/>
 * @class
 * @namespace
 * @this {Select}
 */
class Select extends Embed {

    /**
     * Blot creation.
     * @name create
     * @function
     * @memberof Select
     *
     * @param {Array} options of select options to be appended to the select element.
     *
     * @return {HTMLNode} Newly created HTML Select node.
     */
    static create( options ) {
        let node = document.createElement( Select.tagName );

        if( !Array.isArray( options ) )
            return node;

        let addOption = ( option ) => {
            let optionElement = document.createElement( "option" );

            optionElement.value = escapeHTML( option.value );
            optionElement.innerHTML = escapeHTML( option.title );

            if( option.selected )
                optionElement.setAttribute( "selected", "selected" );

            node.appendChild( optionElement );
        };

        /* Adding a title for this select element. */
        addOption({
            title: "Select",
            value: ""
        });

        /* Add options to the select element. */
        options.forEach( addOption );

        return node;
    }

    /**
     * Return blot value, which is the select element
     * options converted to an array.
     * @name create
     * @function
     * @memberof Select
     *
     * @param {Array} options of select options to be appended to the select element.
     *
     * @return {HTMLNode} Newly created HTML Select node.
     */
    value() {
        let options = [],
            optionElements = this.domNode.options,
            i = 1, L = optionElements.length,
            option = null;

        /* Loops through all options and build values array. */
        for( ; i < L; ++i ) {
            option = optionElements[ i ];

            options.push({
                title: option.innerText,
                value: option.value,
                selected: option.selected
            });
        }

        return { select: options };
    }
}

Select.blotName = "select";
Select.tagName = "select";

/**
 * Match pasted HTML select elements.
 * @name matchSelect
 * @function
 *
 * @param {HTMLNode} node html node of the select element
 * @param {Object}   delta object of the pasted content, generated by Quill.
 *
 * @returns {Object} Delta object with all the necessary options to create a Select blot.
 */
function matchSelect( node, delta ) {
    let options = [],
        optionElements = node.options,
        i = 0, L = optionElements.length,
        option = null;

    /* Loops through all options and build values array. */
    for( ; i < L; ++i ) {
        option = optionElements[ i ];

        options.push({
            title: option.innerText,
            value: option.value
        });
    }

    /* Prepare our delta, which will be consumed by Select class. */
    delta = {
        ops: [{
            insert: { select: options }
        }]
    };

    return delta;
}

/* Add custom matcher to Clipboard module so that we can handle select element copy/paste. */
Clipboard.DEFAULTS.matchers.push( [ Select.tagName, matchSelect ] );

export { Select as default };



